<template>
  <section class="grid-lab">
    <article v-for="(item, article) in video" :key="article">
      <div class="video-over" @click="showModal(item.url)">
        <i class="far fa-play-circle"></i>
      </div>
      <figure>
        <img :src="item.screen" title="Abrir modal" :alt="item.title" />
      </figure>
      <div>
        <h2>{{ item.title }}</h2>
      </div>
    </article>

    <section class="modal" v-show="modal" @click="offModal">
      <div class="modal-block">
        <iframe
          title="renato7oliveira no YouTube!"
          frameborder="0"
          height="500"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
      </div>
    </section>
  </section>
</template>


<script>
export default {
  name: "Lab",
  data() {
    return {
      modal: false,

      video: [
        {
          title: "Game Play: KOF 96",
          url: "https://www.youtube.com/embed/HWgCY43AHxc",
          screen: require("../assets/images/portfolio-video-kof-96.jpg"),
        },
        {
          title: "Game Play: KOF 95",
          url: "https://www.youtube.com/embed/U8PxtB5k0J8",
          screen: require("../assets/images/portfolio-video-kof-95.jpg"),
        },
        {
          title: "Thug Life",
          url: "https://www.youtube.com/embed/DJrxU-av8SI",
          screen: require("../assets/images/portfolio-video-thug-life.jpg"),
        },
        {
          title: "Breaking Bad: Slides de Fotos",
          url: "https://www.youtube.com/embed/qHwl86jkmGg",
          screen: require("../assets/images/portfolio-breaking-bad-video.jpg"),
        },
      ],
    };
  },

  methods: {
    showModal(url) {
      this.modal = true;
      document.querySelector("iframe").src = url;
    },
    offModal() {
      this.modal = false;
      document.querySelector("iframe").src = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.grid-lab {
  // grid-template-columns: repeat(2, 1fr);
  article {
    position: relative;
    .video-over {
      display: grid;
      align-items: center;
      justify-content: center;
      background: rgba($color: #000, $alpha: 0.9);
      height: 100%;
      opacity: 0;
      left: 0px;
      position: absolute;
      top: 0px;
      width: 100%;
      i {
        color: rgba($color: #fff, $alpha: 0.9);
        font-size: 6em;
      }
      &:hover {
        opacity: 1;
      }
    }
  }
}
.modal {
  display: grid;
  align-items: center;
  background: #000;
  .modal-block {
    grid-column: 2 / 7;
    padding-top: 0px;
    iframe {
      width: 100%;
    }
  }
}
.grid-lab {
  article {
    &:hover {
      transform: translateY(0px) scale(1);
    }
    &:active {
      transform: translateY(0px);
    }
  }
}

@media (max-width: 1024px) {
  .grid-lab {
    grid-template-columns: 1fr;
    grid-gap: 15px;
    margin: 0px;
    .modal {
      .modal-block {
        grid-column: 1 / 7;
      }
    }
    iframe {
      width: 100%;
      height: 300px;
    }
  }
}
</style>